import React, { useState, useEffect } from 'react';
import axios from 'axios'

const Mission = () => {
  const [allData, setAllData] = useState(null)

  useEffect(() => {
  axios.get('http://omegasoftpk.ddns.net:4002/mission')
            .then(response => {
              setAllData(response.data)
            })
            .catch(function (error) {
                console.log(error);
            })
          }, [])
    console.log(allData)

    const aboutPara = allData && allData.map( para => {
        return <p className="para" key={para._id}>{para.para}</p>
    })

    const aboutImg = allData && allData.map( image => {
      return <img className="img" key={image._id} src={image.misImg} alt="kanfd" />
  })
  return (
    <div className="centerText">
      <span className="firstheading"><h1>Mission</h1></span>
            <hr className="myHr"/>
        <h3>WE ARE THE CREATIVES AND CREATIVITY NEVER ENDS</h3>
        
        {aboutImg}
{aboutPara}
            <br /><br />
    </div>
  );
}

export default Mission;