import React, { useState, useEffect } from 'react'
import MyCard from './MyCard'
import graphic from '../images/graphic.jpg'
import web from '../images/web.jpg'
import bus from '../images/bus.png'
import market from '../images/market.png'
import saas from '../images/saas.png'
import axios from 'axios'

const Services = () => {
    const [allData, setAllData] = useState(null)

    useEffect(() => {
    axios.get('http://omegasoftpk.ddns.net:4002/service')
              .then(response => {
                setAllData(response.data)
              })
              .catch(function (error) {
                  console.log(error);
              })
            }, [])
      
  
      const aboutPara = allData && allData.map( para => {
          return  <MyCard pic={para.misImg} head={para.head} first={para.first} second={para.second} third={para.third} forth={para.forth} />
      })
console.log(allData)
    return(
        <div className="centerText">
            <span className="firstheading"><h1>Services</h1></span>
            <hr className="myHr"/>
            
           {aboutPara}
        </div>
    )
}
export default Services;