import React, { useState, useEffect} from 'react'
import axios from 'axios'
import { HashRouter, Route, Switch } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import Navbar1 from './components/Navbar';
import Home from './components/Home'
import About from './components/About'
import Mission from './components/Mission';
import Acheivement from './components/Acheivement'
import Services from './components/Services';
import Product from './components/Product'
import Contact from './components/Contact';
import Footer from './components/Footer';
import AddAbout from './formToAddBackend/AddAbout';
import EDITABOUT from './formToAddBackend/EDITABOUT';
import AddMission from './formToAddBackend/AddMission';
import AddAcheivement from './formToAddBackend/AddAcheivement';
import AddProduct from './formToAddBackend/AddProduct';
import AddService from './formToAddBackend/AddService';
import AddSlider from './formToAddBackend/AddSlider';
import SignIn from './formToAddBackend/Signin';
import UpdateAbout from './formToUpdateBackend/UpdateAbout';
//import UpdateAchivement from './formToUpdateBackend/UpdateAchivement';
import UpdateAcheivement from './formToUpdateBackend/UpdateAcheivement'
import UpdateMission from './formToUpdateBackend/UpdateMission';
import UpdateServices from './formToUpdateBackend/UpdateServices';
import UpdateProduct from './formToUpdateBackend/UpdateProduct';


// import UpdateA from './formToUpdateBackend/UpdateAbout';
const App = () => {
  const [allData, setAllData] = useState('')
  useEffect(() => {
    axios.get('/signin')
              .then(response => {
                setAllData(response.data)
              })
              .catch(function (error) {
                  console.log(error);
              })
            }, [])
console.log(allData)
  return (
    <div className="App">
    <HashRouter>
    <Navbar1 />
    <Switch>
     <Route exact path='/'><Home /></Route>
     <Route path='/home'><Home /></Route>
     <Route path='/slider/add'><AddSlider /></Route>
     <Route exact path='/about'><About /></Route>
     <Route path='/about/add'><AddAbout allData={allData}/></Route>
     <Route path='/about/UpdateAbout'><UpdateAbout allData={allData}/></Route>
     <Route path='/acheivement/UpdateAcheivement'><UpdateAcheivement allData={allData}/></Route>
     <Route exact path='/mission'><Mission /></Route>
     <Route path='/mission/add'><AddMission /></Route>
     <Route path='/mission/UpdateMission'><UpdateMission /></Route>
     <Route exact path='/acheivement'><Acheivement /></Route>
     <Route path='/acheivement/add'><AddAcheivement /></Route>
     <Route exact path='/services'><Services   /></Route>
     <Route exact path='/services/UpdateServices'><UpdateServices   /></Route>
     <Route path='/services/add'><AddService /></Route>
     <Route exact path='/product'><Product /></Route>
     <Route path='/product/add'><AddProduct /></Route>
     <Route path='/product/UpdateProduct'><UpdateProduct /></Route>
     <Route path='/contact'><Contact /></Route>
     <Route path='/signin'><SignIn /></Route>
     </Switch>
     <Footer />
     </HashRouter>
    </div>
  );
}
export default App;