import React, { useState, useEffect } from 'react';
import axios from 'axios'


const About = () => {
    const [allData, setAllData] = useState(null)

  useEffect(() => {
  axios.get('http://omegasoftpk.ddns.net:4002/about')
            .then(response => {
              setAllData(response.data)
            })
            .catch(function (error) {
                console.log(error);
            })
          }, [])
    

    const aboutPara = allData && allData.map( para => {
        return <p style={{margin: '45px'}} key={para._id}>{para.para}</p>
    })
    return (
        <div className="About centerText">
          
           <span className="firstheading"><h1 >About</h1></span>
            <hr className="myHr"/>
     {aboutPara}
     <br /><br />
        </div>
    );
}

export default About;