import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { Form, Button } from 'react-bootstrap'
import { Redirect } from 'react-router-dom';

 const UpdateService = () => {
    const [head, setHead] = useState('')
    const [first, setFirst] = useState('')
    const [second, setSecond] = useState('')
    const [third, setThird] = useState('')
    const [forth, setForth] = useState('')
    const [misImg, setImg] = useState()
    const [preUrl, setPreUrl] = useState()
    const [allData, setAllData] = useState('')
    const [email, setEmail] = useState(localStorage.getItem("email"))
    const [password, setPassword] = useState(localStorage.getItem("password"))
 
    const handleChange = (e) => {
        setHead(e.target.value)
    }

    const handleChange1 = (e) => {
        setFirst(e.target.value)
    }

    const handleChange2 = (e) => {
        setSecond(e.target.value)
    }

    const handleChange3 = (e) => {
        setThird(e.target.value)
    }

    const handleChange4 = (e) => {
        setForth(e.target.value)
    }

    useEffect(() => {
        if(misImg){
            const fileReader = new FileReader();
            fileReader.onload= () => {
                setPreUrl(fileReader.result)
            }
            fileReader.readAsDataURL(misImg)
        }
    }, [misImg])

    const handleFileChange = (e) => {
        setImg(e.target.files[0])
    }

    const handleSubmit = (e) => {
        e.preventDefault()
       
        axios.put('http://omegasoftpk.ddns.net:4002/service/update/5ef1a0b6c550d2404461d691', {head, first, second, third, forth, preUrl})
            .then(res => console.log(res.data));
    }
    const show = email && email==='omegasoft@gmail.com' && password  ?
      <>
    <Form style={{marginTop: "100px"}}  method="post" enctype="multipart/form-data" onSubmit={handleSubmit}>
        
         <Form.Group >
          <Form.Label>Head</Form.Label>
          <Form.Control id="head" type="text"  placeholder="Write paragraph" onChange={handleChange} />
        </Form.Group> 
        <Form.Group >
          <Form.Label>First</Form.Label>
          <Form.Control id="first" type="text"  placeholder="Write paragraph" onChange={handleChange1} />
        </Form.Group> 
        <Form.Group >
          <Form.Label>Second</Form.Label>
          <Form.Control id="second" type="text"  placeholder="Write paragraph" onChange={handleChange2} />
        </Form.Group> 
        <Form.Group >
          <Form.Label>Third</Form.Label>
          <Form.Control id="third" type="text"  placeholder="Write paragraph" onChange={handleChange3} />
        </Form.Group> 
        <Form.Group >
          <Form.Label>Forth</Form.Label>
          <Form.Control id="forth" type="text"placeholder="Write paragraph" onChange={handleChange4} />
        </Form.Group> 
        <Form.Group >
          <Form.Label>Image</Form.Label>
          <Form.Control id="misImg" type="file" accept=".jpeg , .png , .jpg" onChange={handleFileChange} />
        </Form.Group>
        
        <Button  variant="primary" type="submit" >Submit</Button>
    
    </Form>

    {preUrl && <img src={preUrl} alt="kdsn" />} 
    </>
       :
       <Redirect to='/signin' /> 
    return(
        show
    )
}
export default UpdateService