import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { Form, Button } from 'react-bootstrap'
import { Redirect } from 'react-router-dom';
import Acheivement from '../components/Acheivement';
import './Update.css'

 const UpdateAcheivement = () => {
    const [para, setAboutPara] = useState('')
    const [misImg, setImg] = useState()
    const [preUrl, setPreUrl] = useState()  
    const [allData, setAllData] = useState('')
    const [email, setEmail] = useState(localStorage.getItem("email"))
    const [password, setPassword] = useState(localStorage.getItem("password"))
 
    useEffect(() => {
        axios.get('http://omegasoftpk.ddns.net:4002/Acheivement')
                .then(response => {
                  setAllData(response.data)
                })
                .catch(function (error) {
                    console.log(error);
                })
                  },[])  
    const handleChange = (e) => {
        setAboutPara(e.target.value)
    }
    useEffect(() => {
        if(misImg){
            const fileReader = new FileReader();
            fileReader.onload= () => {
                setPreUrl(fileReader.result)
            }
            fileReader.readAsDataURL(misImg)
        }
    }, [misImg])

    const handleFileChange = (e) => {
        setImg(e.target.files[0])
    }
    // var id = req.params.id;
    const handleSubmit = (e) => {
        e.preventDefault()
        setAboutPara(para)
        
        axios.put('/acheivement/update/5fce8ed9384f40120191a752', {para, preUrl})
            .then(res => console.log(res.data));
            setAboutPara('')
    }
    const
     AcheivementPara = allData && allData.map( para => {
        return <p style={{margin: '45px'}} key={para._id}>{para.para}</p>
      })
        


    const show = email && email==='omegasoft@gmail.com' && password  ? <>
    <Form style={{marginTop: "100px"}}  method="post" enctype="multipart/form-data" onSubmit={handleSubmit}>
         <Form.Group >
          <Form.Label>Acheivement Paragraph</Form.Label>
          <textarea id="acheivementPara" type="comment" value={para} placeholder="Write paragraph" onChange={handleChange} />
        </Form.Group> 
        <Form.Group >
          <Form.Label>Image</Form.Label>
          <Form.Control id="missionImg" type="file" accept=".jpeg , .png , .jpg" onChange={handleFileChange} />
        </Form.Group>
        
        <Button  variant="primary" type="submit" >Submit</Button>
    
    </Form>

    {preUrl && <img src={preUrl} alt="kdsn" />} 
    </>
       :
       <Redirect to='/signin' />
       return(
        <div className="About centerText">   
           <span className="firstheading"><h1 >UpdateAchievement</h1></span>
            <hr className="myHr"/>
            {AcheivementPara} {show}{para}
        </div>
       )
 }
export default UpdateAcheivement