import React, { useState } from 'react'
import axios from 'axios'

const SignIn = () => {
    const [state , setState] = useState({
        myemail : "",
        mypassword : ""
    })
    
    const handleChange = e => {
        const {name , value} = e.target
        setState( prevState => ({
            ...prevState,
            [name] : value
        }))
    }
      const handleSubmit = (e) => {
        e.preventDefault();
        const email =state.myemail
        const password = state.mypassword
        localStorage.setItem("email", email);
        localStorage.setItem("password", password);
      
      console.log(email, password)
      console.log(state.myemail, state.mypassword)
      }
    return(
      
    <div className="login" style={{marginTop: "100px"}}>
      <h1>Login</h1>
      <form method="POST" onSubmit={handleSubmit}>
      
        <br /> 
        <div className="form-group">        
          <label htmlFor="email">Email :</label>
          <input type="email" name="myemail" id="myemail" placeholder="Enter your id" onChange={handleChange} />
        </div><br />

        <div className="form-group">
          <label htmlFor="password">Password</label>
          <input type="password" name="mypassword" id="mypassword" placeholder="Enter your password" onChange={handleChange} />
        </div><br />

        <input type="submit" value="Submit" />
        
      </form>
      
   <br /><br />
  </div>
    )
}
export default SignIn