import React, { useState, useEffect } from 'react'
import { Carousel } from 'react-bootstrap'
import first from '../images/first.jpg'
import sec from '../images/acheivement.jpg'
import third from '../images/mission.jpg'
import axios from 'axios'

const Slider = () => {
  const [allData, setAllData] = useState(null)

    useEffect(() => {
    axios.get('http://omegasoftpk.ddns.net:4002/slider')
              .then(response => {
                setAllData(response.data)
              })
              .catch(function (error) {
                  console.log(error);
              })
            }, [])
      
  
      const aboutPara = allData && allData.map( para => {
          return <Carousel.Item>
          <img
            className="d-block w-100"
            src={para.misImg}
            height="600px"
            alt="First slide"
          />
          <Carousel.Caption>
            <h3>{para.head}</h3>
            <p>{para.para}</p>
          </Carousel.Caption>
        </Carousel.Item>
      })

  return (
    <div className="slider">
    <Carousel>
 {aboutPara}
</Carousel>
</div>
    )
}
export default Slider;