import React, { useState, useEffect } from 'react'
import Sample from './Sample'
import brand1 from '../images/brand 1.jpg' 
import brand2 from '../images/brand 2.jpg' 
import brand3 from '../images/brand 3.jpg' 
import brand4 from '../images/brand 4.jpg' 
import brand5 from '../images/brand 5.jpg' 
import brand6 from '../images/brand 6.jpg' 
import axios from 'axios'

const Product = () => {
    const [allData, setAllData] = useState(null)

    useEffect(() => {
    axios.get('http://omegasoftpk.ddns.net:4002/product')
              .then(response => {
                setAllData(response.data)
              })
              .catch(function (error) {
                  console.log(error);
              })
            }, [])
      
  
    

      
    const aboutImg = allData && allData.map( image => {
        return <Sample pic={image.misImg} />
    })
    console.log(allData)
    return (
        <div className="centerText">
        <span className="firstheading"><h1>Product</h1></span>
        <hr className="myHr"/>
            {aboutImg}
        </div>
    )
}
export default Product