import React, { useState, useEffect } from 'react';
import Pic from '../images/acheivement.jpg'
import axios from 'axios'

const Acheivement = () => {
    const [allData, setAllData] = useState(null)

    useEffect(() => {
    axios.get('http://omegasoftpk.ddns.net:4002/acheivement')
              .then(response => {
                setAllData(response.data)
              })
              .catch(function (error) {
                  console.log(error);
              })
            }, [])
      
  
      const aboutPara = allData && allData.map( para => {
          return <p className="para" key={para._id}>{para.para}</p>
      })

      
    const aboutImg = allData && allData.map( image => {
        return <img className="img" style={{height: "300px"}} key={image._id} src={image.misImg} alt="kanfd" />
    })
    return(
        <div className="centerText">
            <span className="firstheading"><h1>Acheivement</h1></span>
            <hr className="myHr"/>
            <h3>YOUR IMAGINATION IS OUR CREATION</h3>
         
            
            {aboutImg}

            {aboutPara}
            <br /><br />
        </div>
    )
}
export default Acheivement;