import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { Form, Button } from 'react-bootstrap'
import { Redirect } from 'react-router-dom';
import './Update.css'


 const UpdateMission = () => {
    const [para, setAboutPara] = useState('')
    const [misImg, setImg] = useState()
    const [preUrl, setPreUrl] = useState()
    const [allData, setAllData] = useState('')
    const [email, setEmail] = useState(localStorage.getItem("email"))
    const [password, setPassword] = useState(localStorage.getItem("password"))
 
useEffect(() => {
    if(misImg){
        const fileReader = new FileReader();
        fileReader.onload= () => {
            setPreUrl(fileReader.result)
        }
        fileReader.readAsDataURL(misImg)
    }
}, [misImg])

    const handleChange = (e) => {
        setAboutPara(e.target.value)
    }

    const handleFileChange = (e) => {
        setImg(e.target.files[0])
    }

    useEffect(() => {
        axios.get('http://omegasoftpk.ddns.net:4002/Mission')
                .then(response => {
                  setAllData(response.data)
                })
                .catch(function (error) {
                    console.log(error);
                })
                  },[]) 

    const handleSubmit = (e) => {
        e.preventDefault()
        setAboutPara(para)
        axios.put('http://omegasoftpk.ddns.net:4002/mission/update/5fce8f6c384f40120191a753', {para, preUrl})
            .then(res => console.log(res));

           
            setAboutPara('')
    }
    console.log(para)
    console.log(misImg)
    const
    MissionPara = allData && allData.map( para => {
       return <p style={{margin: '45px'}} key={para._id}>{para.para}</p>
     })
    const show = email && email==='omegasoft@gmail.com' && password  ?
     <>
    <Form style={{marginTop: "100px"}} method="post" enctype="multipart/form-data" onSubmit={handleSubmit}>
      
       <Form.Group >
        <Form.Label>About Paragraph</Form.Label>
        <textarea id="missionPara" type="comment" value={para} placeholder="Write paragraph" onChange={handleChange} />
      </Form.Group> 
      <Form.Group >
        <Form.Label>Image</Form.Label>
        <Form.Control id="missionImg" type="file" accept=".jpeg , .png , .jpg" onChange={handleFileChange} />
      </Form.Group>
      
      <Button  variant="primary" type="submit" >Submit</Button>
  
  </Form>
{preUrl && <img src={preUrl} alt="kdsn" />} 


  </>
   :
   <Redirect to='/signin' /> 

   return(
    <div className="About centerText">   
       <span className="firstheading"><h1 >UpdateMission</h1></span>
        <hr className="myHr"/>
        {MissionPara} {show}
    </div>
   )

}
export default UpdateMission