import React from 'react';
import { Form, Button } from 'react-bootstrap'

const Contact = () => {
    return(
      <div className="centerText">
            <span className="firstheading"><h1>Contact</h1></span>
            <hr className="myHr"/><br />
        <Form id="form">
        <Form.Group>
    <Form.Control type="text" placeholder="Enter name" />
  </Form.Group>

  <Form.Group>
    <Form.Control type="email" placeholder="Enter email" />
  </Form.Group>

  <Form.Group>
    <Form.Control type="text" placeholder="Enter mobile no" />
  </Form.Group>

  <Form.Group>
    <Form.Control type="commment" placeholder="Enter message" />
  </Form.Group>
<br />
  <Button variant="primary" className="butt" type="submit">
    Submit
  </Button>
</Form>
<br /><br />
</div>
    )
}
export default Contact;