import React from 'react'
import { Card, ListGroupItem, ListGroup } from 'react-bootstrap'

const MyCard = prop => {
    return(
        
            <Card className="serCard">
  <Card.Img variant="top" height="190px" width="300px" src={prop.pic} />
  <Card.Body>
    <Card.Title>{prop.head}</Card.Title>
  </Card.Body>
  <ListGroup className="list-group-flush">
    <ListGroupItem>{prop.first}</ListGroupItem>
    <ListGroupItem>{prop.second}</ListGroupItem>
    <ListGroupItem>{prop.third}</ListGroupItem>
    <ListGroupItem>{prop.forth}</ListGroupItem>
  </ListGroup>
</Card>
       
    )
}
export default MyCard;