import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { Form, Button } from 'react-bootstrap'
import { Redirect } from 'react-router-dom';

 const AddMission = () => {
    const [para, setAboutPara] = useState('')
    const [misImg, setImg] = useState()
    const [preUrl, setPreUrl] = useState()
    const [allData, setAllData] = useState('')
    const [email, setEmail] = useState(localStorage.getItem("email"))
    const [password, setPassword] = useState(localStorage.getItem("password"))
 
useEffect(() => {
    if(misImg){
        const fileReader = new FileReader();
        fileReader.onload= () => {
            setPreUrl(fileReader.result)
        }
        fileReader.readAsDataURL(misImg)
    }
}, [misImg])

    const handleChange = (e) => {
        setAboutPara(e.target.value)
    }

    const handleFileChange = (e) => {
        setImg(e.target.files[0])
    }

    const handleSubmit = (e) => {
        e.preventDefault()
        setAboutPara(para)
        axios.post('http://omegasoftpk.ddns.net:4002/mission/add', {para, preUrl})
            .then(res => console.log(res));

           
            setAboutPara('')
    }
    console.log(para)
    console.log(misImg)

    const show = email && email==='admin@my.coms' && password  ?
     <>
    <Form style={{marginTop: "100px"}} method="post" enctype="multipart/form-data" onSubmit={handleSubmit}>
      
       <Form.Group >
        <Form.Label>About Paragraph</Form.Label>
        <Form.Control id="missionPara" type="comment" value={para} placeholder="Write paragraph" onChange={handleChange} />
      </Form.Group> 
      <Form.Group >
        <Form.Label>Image</Form.Label>
        <Form.Control id="missionImg" type="file" accept=".jpeg , .png , .jpg" onChange={handleFileChange} />
      </Form.Group>
      
      <Button  variant="primary" type="submit" >Submit</Button>
  
  </Form>
{preUrl && <img src={preUrl} alt="kdsn" />} 


  </>
   :
   <Redirect to='/signin' /> 

    return(
        show
    )
}
export default AddMission